package widgets

import kotlinx.browser.document
import kotlinx.html.dom.create
import kotlinx.html.id
import kotlinx.html.js.div
import kotlinx.html.style
import org.w3c.dom.HTMLDivElement
import toPx

class AdBanner {
    val heightToWidth = 62.0/600.0

    fun expectedHeight(width: Int): Int {
        return (width*heightToWidth).toInt()
    }

    fun generate(width: Int): HTMLDivElement {
        val height = expectedHeight(width)
        val bannerBox = document.create.div{
            id ="adBannerHolder"
            style = """
                        margin: auto;
                        width: ${toPx(width)};
                        height: ${toPx(height)};
                        background-color: #171819;
                    """
            /*
                background-image: url("/icon/banner_placeholder.png");
                background-repeat: no-repeat;
                background-size: 200% ${toPx(height*2)};
                background-position: center;
             */
        }
        val p = document.create.div {
            style = """
                text-align: center;
                line-height:  ${toPx(height)};
                width: 100%;
                color: #FF7B00;
                font-family: guessFont, sans-serif;
                font-size: ${toPx(height * 0.4)};
            """
            text(adFunction().first)
        }
        bannerBox.append(p)
        return bannerBox
    }

    private fun adFunction(): Pair<String, String> {
        // TODO real add
        val games = listOf("RePair de","Reconsider da","Sock Monster da","Secret Santa da","Not Escape Room da","Are We There Yet de")
        val links = listOf("https://pungocorp.itch.io/repair",
            "https://pungocorp.itch.io/reconsider",
            "https://pungocorp.itch.io/sockmonster",
            "https://celik97.itch.io/find-the-passcode-jam",
            "https://pungocorp.itch.io/not-escape-room",
            "https://pungocorp.itch.io/are-we-there-yet")
        val game = games.indices.random()
        return Pair("PunGo'dan ${games[game]} gzel oyun.", links[game])
    }
}