package game

import Constants
import Stats
import kotlinx.browser.window

class Game(var letters: Int, val attempts: Int, val stats: Stats) {
    var recordedWord = ""
    var activeRow = 0
    val wordList = mutableListOf<String>()
    var activeWordList = listOf<String>()
    var gameWord = ""
    var gameSolved = false
    var gameLost = false
    val playable: Boolean
    get() {
        return !gameSolved && !gameLost
    }
    var guessDatabase = mutableListOf<Guess>()
    val gridRate: Double
    get() {
        return letters.toDouble()/attempts.toDouble()
    }


    fun loadWords(filepath: String, andStart: Boolean = false, seed: Int = -1) {
        val url = window.location.href.split("?")[0]
        Constants.dictionary.getAsync(url + filepath) { list ->
            wordList.addAll(list.lines().filter { !it.contains(' ') })
            if (andStart) {
                startRound(seed = seed)
            }
        }
    }

    fun startRound(letters: Int = this.letters, seed: Int = -1) {
        activeRow = 0
        guessDatabase.clear()
        gameSolved = false
        gameLost = false
        if (letters!=this.letters){
            this.letters = letters
        }
        activeWordList = wordList.filter {
            it.length == letters
        }
        if (seed == -1) {
            var newSeed = activeWordList.indices.random()
            selectWord(activeWordList.indices.random())
        } else {
            selectWord(seed)
        }
    }

    fun selectWord(seed: Int = -1) {
        gameWord = activeWordList[seed]
    }

    fun createChallenge(): String {
        return window.location.href.split("?")[0] + "?seed=" + activeWordList.indexOf(gameWord)
    }

    fun addLetter(c: Char) {
        if (recordedWord.length < letters) {
            recordedWord += c
        } else {
            // too long warning raises here
        }
    }

    fun removeLetter() {
        recordedWord = recordedWord.dropLast(1)
    }

    fun checkWord() {
        if(activeWordList.isEmpty()){
            window.alert("kelime yklenmedi")
        }

        if (recordedWord.length != letters) { // not enough letters
            window.alert("Yetersiz harf.")
            return
        }

        if(recordedWord=="pungo"){
            window.alert("Biz de sizi ok seviyoruz.")
        }

        if(recordedWord !in activeWordList){ // not a word
            window.alert("Byle kelime olmaz olsun.")
            return
        }

        // rock & roll
        val newGuesses = (0 until letters).map {i-> Guess(recordedWord[i],i,-1,activeRow)}
        val gamePairs = (0 until letters).map { i-> Pair(i,gameWord[i]) }.toMutableList()


       // var miniRecord = gameWord.toMutableList()
        var miniIndex = 0
        newGuesses.forEachIndexed {index, it->
            if(gameWord[it.slot]==it.letter){
                newGuesses[index].result = GuessResult.RIGHT_PLACE
                newGuesses[index].matchSlot = gamePairs[miniIndex].first
                gamePairs.removeAt(miniIndex)
            }else{
                miniIndex += 1
            }
        }

        if(gamePairs.isEmpty()){
            gameSolved = true
            guessDatabase.addAll(newGuesses)
            return
        }





        newGuesses.forEachIndexed{index, it->
            if(it.result!=GuessResult.RIGHT_PLACE){
                val ind = gamePairs.indexOfFirst {gp-> gp.second==it.letter  }
                if(ind!=-1){
                    newGuesses[index].result = GuessResult.WRONG_PLACE
                    newGuesses[index].matchSlot = gamePairs[ind].first
                    gamePairs.removeAt(ind)
                }else{
                    newGuesses[index].result = GuessResult.WRONG
                }
            }
        }

        guessDatabase.addAll(newGuesses)
        if(activeRow<attempts){
            recordedWord = ""
            activeRow+=1
        }else{
            gameLost = true
        }

        return

        /*


        val greenList = mutableListOf<Int>()
        val yellowList = mutableListOf<Int>()

        val recordedList = recordedWord.toMutableList()
        val recordedIndices = (0 until letters).toMutableList()
        val gameList = gameWord.toMutableList()
        val gameIndices = (0 until letters).toMutableList()


