package widgets

import kotlinx.browser.document
import kotlinx.html.button
import kotlinx.html.dom.create
import kotlinx.html.id
import kotlinx.html.js.div
import kotlinx.html.js.onClickFunction
import kotlinx.html.style
import org.w3c.dom.HTMLDivElement
import toPx

class MidBanner() {
    lateinit var challenge: () -> Unit
    val heightToWidth = 72.0/600.0 // height/width

    fun expectedHeight(width: Int): Int {
        return (width*heightToWidth).toInt()
    }

    fun generate(width: Int): HTMLDivElement {
        val height = expectedHeight(width)
        val topPart = document.create.div{
            style="""
                height: ${toPx(height)};
                width: ${toPx(width)};
            """
        }.also {
            it.id ="midbanner"
        }


        val timer = document.create.div{
            id="timer"
            style =
                """
                        height: ${toPx(height)};
                        width: ${toPx(width * 0.3)};
                        opacity: 1;
                        float: left;
                        font-family: openSans, sans-serif;
                        font-size: ${toPx(height * 0.6)};
                    """
        }
        val d = document.create.div{
            text("00:00")
        }.also {
            it.style.textAlign="center"
            it.style.lineHeight = toPx(height)
            it.style.width="100%"
        }

        timer.append(d)
        topPart.append(
            timer
        )




        val buttonHole = document.create.div{
            id="challenge div"
            style =
                """
                    float: right;
                    height: ${toPx(height)};
                    width: ${toPx(width * 0.6)};
                    opacity: 1;
                """
        }
        document.create.button{
            text("Meydan Oku")
            onClickFunction = {
                challenge()
            }
        }.also {
            it.className = "challengeButton"
            it.style.fontSize = toPx(height * 0.4)
            it.style.height = toPx(height * 0.8)
            it.style.width= toPx(width * 0.6 * 0.8)
            it.style.marginTop = toPx(height * 0.1)
            it.style.marginBottom = toPx(height * 0.1)
            it.style.marginLeft = toPx(width * 0.6 * 0.1)
            it.style.marginRight = toPx(width * 0.6 * 0.1)
            buttonHole.append(it)
        }

        topPart.append(buttonHole)
        return topPart
    }

    fun updateTimer(s: String){
        document.getElementById("timer")!!.innerHTML=s
    }

}