import org.w3c.xhr.XMLHttpRequest

class Dictionary {
    private val dictURL = "https://sozluk.gov.tr/gts?ara="

    fun getAsync(url: String, callback: (String) -> Unit) {
        val xmlHttp = XMLHttpRequest()
        xmlHttp.open("GET", url)
        xmlHttp.onload = {
            if (xmlHttp.readyState == 4.toShort() && xmlHttp.status == 200.toShort()) {
                callback.invoke(xmlHttp.responseText)
            }
        }
        xmlHttp.send()
    }

    fun lookup(word: String) {
        val url = dictURL + word
        getAsync(url) { it ->
            val res : Anlam = JSON.parse(it)
            println("FULL: " + JSON.stringify(res))
            println("maddeID: " + res.cesit)
        }
    }

}
data class Anlam (
    val madde_id: String,
    val kac: String,
    val kelimeNo: String,
    val cesit: String,
    val anlamGor: String,
    val onTaki: Any? = null,
    val madde: String,
    val cesitSay: String,
    val anlamSay: String,
    val taki: Any? = null,
    val cogulMu: String,
    val ozelMi: String,
    val lisanKodu: String,
    val lisan: String,
    val telaffuz: Any? = null,
    val birlesikler: String,
    val font: Any? = null,
    val maddeDuz: String,
    val gosterimTarihi: Any? = null,
    val anlamlarListe: List<AnlamlarListe>,
    val atasozu: List<Atasozu>
