fun main() {
    val played = if (accessCookie("played").toIntOrNull() == null) 0 else accessCookie("played").toInt()
    val win = if (accessCookie("win").toIntOrNull() == null) 0 else accessCookie("win").toInt()
    val streak = if (accessCookie("streak").toIntOrNull() == null) 0 else accessCookie("streak").toInt()
    val maxStreak = if (accessCookie("maxStreak").toIntOrNull() == null) 0 else accessCookie("maxStreak").toInt()
    val record = if (accessCookie("record").toIntOrNull() == null) 0 else accessCookie("record").toInt()
    val dist = mutableListOf<Int>()
    (1..6).forEach {
        dist.add(if (accessCookie("d$it").toIntOrNull() == null) 0 else accessCookie("d$it").toInt())
    }
    val stats = Stats(played, win, streak, maxStreak, record, dist)
    val game = Game(Constants.letterLimit, Constants.attempts, stats)

    document.body?.sayHello(game = game)
    if (window.location.href.contains("seed")) {
        try {
            val seed = window.location.href.split("?")[1].split("=")[1]
            game.loadWords("toLoad/gapless.txt", true, seed = seed.toInt())
        } catch (e: Exception) {
            game.loadWords("toLoad/gapless.txt", true)
        }
    } else {
        game.loadWords("toLoad/gapless.txt", true)
    }

    //      Constants.dictionary.lookup("kalem")
    //      generateCookie("color", c)
    //      accessCookie("color"))
}

fun updateTimer() {
    if (timerActive) {
        Constants.seconds++
        val m = Constants.seconds / 60
        val s = Constants.seconds % 60
        val mt = if (m < 10) "0$m" else m.toString()
        val st = if (s < 10) "0$s" else s.toString()
        midBanner.updateTimer("$mt:$st")
    }
}


fun generateCookies(names: List<String>, values: List<String>) {
    val ex = "expires=2038-01-18 04:14:07"
    names.indices.forEach {
        document.cookie = "${names[it]}=${values[it]};$ex"
    }
}


fun accessCookie(name: String): String {
    val value = document.cookie.split("; ").find { row -> row.startsWith(name) }
    return if (value != null) value.split('=')[1]
    else ""
}

