package widgets

import game.Guess
import kotlinx.browser.document
import kotlinx.html.dom.create
import kotlinx.html.id
import kotlinx.html.js.div
import kotlinx.html.style
import org.w3c.dom.HTMLDivElement
import org.w3c.dom.HTMLElement
import toPx

class GridPart(val width: Double, val height: Double, val attempts: Int, val letters: Int) {
    var activeRow = 0
    val trianglePositions = (0 until attempts).map { triangleFromTop(it) }
    val w: Int
    val h: Int
    init {
        if((width/letters*attempts)>height){
            h = (height).toInt()
            w = (height/attempts*letters).toInt()
        }else{
            w = width.toInt()
            h = (width/letters*attempts).toInt()
        }
    }

    fun createGrid(): HTMLDivElement {
        val gridFrame = document.create.div{
            id = "centre_part"
            style = """
                        height: ${toPx(height)};
                    """
        }



        val gridDiv = document.create.div{
            id = "grid_div"
            style = """
                        margin-left: ${toPx((width - w) * 0.5)};
                        width: ${toPx(w)};
                        margin-top: ${toPx((height - h) * 0.5)};
                        height: ${toPx(h)};
                        font-family: testFont;
                        box-sizing: border-box;
                        position: relative;
                    """
        }

        for (i in 0 until attempts){
            for (j in 0 until letters){
                val d = document.create.div {
                    style = """
                        border: solid;
                        opacity: .8;
                        width: ${toPx(w / letters)};
                        height: ${toPx(h / attempts)};
                        float: left;
                        box-sizing: border-box;
                    """
                }

                document.create.div {
                    id = "slot_${i}_$j"
                    text("")
                    style = """
                            font-family: guessFont, sans-serif;
                            font-size: ${toPx(h / attempts * 0.7)};
                            text-align: center;
                            line-height: ${toPx(h / attempts)};
                            width: 100%;
                            height: 100%;
                        """

                }.also {
                    d.append(it)
                }



                gridDiv.append(d)
            }
        }
        gridFrame.append(gridDiv)




        val triangle = document.create.div {
            style = """
                width: 0; 
                height: 0; 
                border-top: ${toPx(w / attempts * 0.2)} solid transparent;
                border-bottom: ${toPx(w / attempts * 0.2)} solid transparent;  
                border-left: ${toPx(w / attempts * 0.2)} solid  #110A01;
                position: absolute;
                left: 0;
                top: ${toPx(triangleFromTop(0))};
            """
            id = "triangle"
        }

        gridDiv.append(triangle)


        return gridFrame
    }


    /** This takes the last row
     *
     */
    fun paintRow(l: List<Guess>){
        l.forEach {
            (document.getElementById("slot_${it.attempt}_${it.slot}")!! as HTMLElement).style.backgroundColor = it.result.colour()
        }
    }

    fun paintRow(l: Pair<List<Int>, List<Int>>){ //green index, yellow index
        for (i in 0 until letters){
            (document.getElementById("slot_${activeRow}_${i}")!! as HTMLElement).style.backgroundColor = when (i) {
                in l.first -> {
                    "#11BB11"
                }
                in l.second -> {
                    "#BBBB11"
                }
                else -> {
                    "#666666"
                }
            }
        }

    }

    fun clear(){
        for( i in 0 until attempts){
            for (j in 0 until letters){
                (document.getElementById("slot_${i}_${j}")!! as HTMLElement).also {
                    it.style.backgroundColor = "#EDEAEA"
                    it.innerText=""
                }

            }
        }
    }


    fun triangleFromTop(row: Int): Double {
        return (h/attempts)*(row+0.5) - w/attempts*0.2

    }

    fun moveTriangle(){
        (document.getElementById("triangle") as HTMLElement).also {
            it.style.top = toPx(triangleFromTop(activeRow))
        }
    }



    fun writeLetter(s: String){
        for (i in 0 until letters){
            //document.getElementById("slot_${activeRow}_${i}")!!.innerHTML =

            document.getElementById("slot_${activeRow}_${i}")!!.textContent = turkishUppercase(s[i]).toString()

        }
    }

    fun turkishUppercase(c: Char): Char {
        return if(c==''){
            'I'
        }else if(c=='i'){
            ''
        }else{
            c.uppercaseChar()
        }
    }
}