import game.Game
import kotlinx.browser.window

/** This calculates relevant sizes for the app
 * it can one day be used for resize
 */
class SizeCalculator(ultra: Double, game: Game){
    val appWidth: Int
    val gridHeight: Double
    init {
        val padHeight = (window.innerHeight-20).toDouble()
        val padWidth = (window.innerWidth).toDouble()
        val gridRate = game.gridRate
        val squeeze = (padHeight-ultra* padWidth)/padWidth*gridRate

        val newSqueeze: Double
        if(squeeze>1.0){ //tall
            appWidth = padWidth.toInt()
            newSqueeze = 1.0
        }else if(squeeze<Constants.accordeonLimit){ // fat
            appWidth = (padHeight/(ultra + Constants.accordeonLimit/gridRate)).toInt()
            newSqueeze = Constants.accordeonLimit
        }else{ // fish meat
            appWidth = padWidth.toInt()
            newSqueeze = squeeze
        }
        gridHeight = appWidth/gridRate*newSqueeze
    }
}