package widgets

import kotlinx.browser.document
import kotlinx.html.button
import kotlinx.html.dom.create
import kotlinx.html.id
import kotlinx.html.js.div
import kotlinx.html.js.onClickFunction
import kotlinx.html.style
import org.w3c.dom.HTMLDivElement
import toPx

class TopPart {
    var replayFunction = {}
    val heightToWidth = 72.0/600.0 // height / width

    fun expectedHeight(width: Int): Int {
        return (width*heightToWidth).toInt()
    }

    fun generate(width: Int): HTMLDivElement {
        val height = expectedHeight(width)
        val topPart = document.create.div {
            id = "topPartWidget"
            style = """
                         margin: auto;
                         height: ${toPx(height)};
                         width: ${toPx(width)}
                    """
        }
        val title = document.create.div{
            id="titleDiv"
            style =
                """
                        height:  ${toPx(height)};
                        width: 40%;
                        text-align: center;
                        float: left;
                        font-family: titleFont, sans-serif;
                        font-size: ${toPx(height * 0.7)};
                        text-align: center;
                        line-height: ${toPx(height)};
                    """

            text("SZCK")
        }
        topPart.append(title)


        val buttonTexts = listOf("op","st","re","he")
        val buttonName = listOf("settings","stats","replay","howto")

        val extra = if(document.URL.contains("localhost")){
            ""
        }else{
            "/sozck"
        }

        for (i in 0..3){
            val buttonHole = document.create.div{
                id="titleDiv"
                style =
                    """
                        float: right;
                        height:  ${toPx(height)};
                        width:  ${toPx(height)};
                        text-align: center;
                        margin-top: auto;
                    """
            }



            document.create.button{
                onClickFunction = {event->
                    buttonTask(i)
                }
                style = """
                        opacity: 1;
                        width:  ${toPx(height * 0.8)};
                        border: none;
                        height: ${toPx(height * 0.8)};
                        margin-top: ${toPx(height * 0.1)};
                        background-image: url("${extra}/icon/${buttonName[i]}.png");
                        background-repeat: no-repeat;
                        background-size: ${toPx(height * 0.5)} ${toPx(height * 0.5)};
                        background-position: center;
                    """
            }.also {
                buttonHole.append(it)
            }
            topPart.append(buttonHole)
        }
        return topPart
    }

    fun buttonTask(buttonNo: Int){
        when(buttonNo){
            0 -> {
                // TODO options
            }
            1 -> {
                // TODO statistics
            }
            2 -> {
                replayFunction()
                // TODO replay
            }
            3 -> {
                // TODO help
            }
        }
    }
}